.speacialNavbar {
  width: 100vw;
  height: 80px;
  background-color: black;
  justify-content: center;
}
.gamesSliderBoxx div {
  margin-right: auto;
  margin-left: auto;
  text-decoration: none;
  cursor: pointer;
}
.sticky {
  position: fixed;
  bottom: 0;
  width: 100vw;
  z-index: 1;
  text-decoration: none;
}
.gamesSliderBoxx {
  color: #fff;
  background: #000;
  width: 100%;
  height: 50px;
}
.icon {
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  margin-top:10px;
  cursor: pointer;
}
