nav .inLine {
  display: inline-block;
  font-size: 20px;
  list-style-type: none;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
nav li {
  display: inline;
}
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  justify-content: center;
}

nav li {
  float: left;
}

nav li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  color: red !important;
}

nav li div {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  font-size: 14px;
  text-decoration: none;
  color: red !important;
  cursor: pointer;
}
nav li span {
  color: green;
  text-align: center;
  font-size: 15px;
  display: flex;
  text-decoration: none;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

/* Change the link color to #111 (black) on hover */
nav li Link:hover {
  background-color: #111;
}

.myNavbar {
  background-color: #000 !important;
  width: 100%;
  margin-bottom: 5px;


}
.bg-green {
  background-color: #999;
}
.announceBox {
  font-size: 15px;
  width: 100%;
  font-weight: 500;
  margin: auto;
  align-items: center;
  display: flex;
  justify-content: center;
  color: red;
  box-shadow: 10px;
  text-transform: uppercase;
  overflow: hidden;
}
