@charset "UTF-8";
/*

*
*/
a:focus,
button:focus {
  outline: none !important;
}

button::-moz-focus-inner {
  border: 0;
}

*:focus {
  outline: none;
}

blockquote {
  padding: 0;
  margin: 0;
}

input,
button,
select,
textarea {
  outline: none;
}

label {
  margin-bottom: 0;
}

p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul li,
ol li {
  display: block;
}

dl {
  margin: 0;
}

dt,
dd {
  line-height: inherit;
}

dt {
  font-weight: inherit;
}

dd {
  margin-bottom: 0;
}

cite {
  font-style: normal;
}

form {
  margin-bottom: 0;
}

blockquote {
  padding-left: 0;
  border-left: 0;
}

address {
  margin-top: 0;
  margin-bottom: 0;
}

figure {
  margin-bottom: 0;
}

html p a:hover {
  text-decoration: none;
}

/*
*
* Typography
*/
body {
  display: block;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.71429;
  font-weight: 400;
  letter-spacing: 0.025em;
  color: #9b9b9b;
  background-color: #fff;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: subpixel-antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6,
[class*="heading-"] {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-weight: 500;
  letter-spacing: 0.05em;
  color: #151515;
  text-transform: uppercase;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
[class*="heading-"] a {
  color: inherit;
}

h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover,
[class*="heading-"] a:hover {
  color: #64b000;
}

h1,
.heading-1 {
  font-size: 32px;
  line-height: 1.3;
}

@media (min-width: 768px) {
  h1,
  .heading-1 {
    font-size: 40px;
    line-height: 1.15;
  }
}

@media (min-width: 1200px) {
  h1,
  .heading-1 {
    font-size: 60px;
    line-height: 1.16667;
  }
}

h2,
.heading-2 {
  font-size: 28px;
  line-height: 1.5;
}

@media (min-width: 1200px) {
  h2,
  .heading-2 {
    font-size: 48px;
    line-height: 1.25;
  }
}

h3,
.heading-3 {
  font-size: 24px;
  line-height: 1.5;
}

@media (min-width: 1200px) {
  h3,
  .heading-3 {
    font-size: 33px;
    line-height: 1.33333;
  }
}

h4,
.heading-4,
h5,
.heading-5,
h6,
.heading-6 {
  letter-spacing: 0.025em;
}

h4,
.heading-4 {
  font-size: 20px;
  line-height: 1.35;
}

@media (min-width: 1200px) {
  h4,
  .heading-4 {
    font-size: 24px;
    line-height: 1.25;
  }
}

h5,
.heading-5 {
  font-size: 18px;
  line-height: 1.35;
}

@media (min-width: 1200px) {
  h5,
  .heading-5 {
    font-size: 18px;
    line-height: 1.22222;
  }
}

h6,
.heading-6 {
  font-size: 14px;
  line-height: 1.35;
}

@media (min-width: 1200px) {
  h6,
  .heading-6 {
    font-size: 16px;
    line-height: 1.25;
  }
}

.heading-7 {
  font-size: 14px;
  line-height: 1.35;
  letter-spacing: 0.05em;
}

.heading-decoration-1 {
  position: relative;
}

.heading-decoration-1::before {
  content: "";
  position: relative;
  top: 0.55em;
  display: inline-block;
  vertical-align: top;
  width: 0.83333em;
  margin-right: 0.3em;
  border-bottom: 3px solid;
}

.heading-decoration-2 {
  position: relative;
  padding-left: 40px;
}

.heading-decoration-2::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.5em;
  width: 25px;
  height: 3px;
  display: inline-block;
  vertical-align: middle;
  background: #ffd03a;
}

* + .heading-decoration-2 {
  margin-top: 20px;
}

small,
.small {
  display: block;
  font-size: 12px;
  line-height: 1.5;
}

mark,
.mark {
  padding: 3px 5px;
  color: #fff;
  background: #2c343b;
}

.big {
  font-size: 16px;
  line-height: 1.75;
}

.lead {
  font-size: 24px;
  line-height: 34px;
  font-weight: 300;
}

code {
  padding: 10px 5px;
  border-radius: 0.2rem;
  font-size: 90%;
  color: #111;
  background: #edeff4;
}

p [data-toggle="tooltip"] {
  padding-left: 0.25em;
  padding-right: 0.25em;
  color: #64b000;
}

p [style*="max-width"] {
  display: inline-block;
}

.text-style-1 {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 12px;
  line-height: 1.4;
  font-weight: 500;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #9b9b9b;
}

.text-style-2 {
  position: relative;
  overflow: hidden;
  padding-left: 35px;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.4;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.text-style-2 span {
  position: relative;
  padding-left: 10px;
}

.text-style-2 span::before {
  position: absolute;
  top: 50%;
  transform: translate3d(-100%, -50%, 0);
  left: 0;
  content: "";
  border-bottom: 1px solid;
  width: 100vw;
}

@media (min-width: 768px) {
  .text-style-2 {
    padding-left: 75px;
  }
}

.text-style-2 + * {
  margin-top: 15px;
}

.footer-classic-dark .text-style-2,
.context-dark .text-style-2,
.rd-navbar-dark .text-style-2,
.rd-navbar-darker .text-style-2,
.bg-gray-700 .text-style-2,
.bg-gray-2 .text-style-2,
.bg-gray-4 .text-style-2,
.bg-primary .text-style-2,
.bg-image-dark .text-style-2 {
  color: rgba(255, 255, 255, 0.6);
}

.text-style-3 {
  font-weight: 700;
  font-style: italic;
}

.text-style-4 {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  font-weight: 400;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

* + .text-style-4 {
  margin-top: 5px;
}

.heading-component {
  position: relative;
  line-height: 1;
  border-bottom: 1px solid #e1e1e1;
}

.heading-component-inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-left: -15px;
}

.heading-component-inner > * {
  margin-left: 15px;
}

.heading-component-inner > * {
  margin-bottom: 11px;
}

.heading-component-inner * + .button {
  margin-top: 0;
}

.heading-component-title {
  position: relative;
  display: inline-block;
  padding-left: 15px;
  line-height: 26px;
}

.heading-component-title::before {
  content: "";
  position: absolute;
  top: 3px;
  bottom: 3px;
  left: 0;
  border-left: 4px solid #64b000;
  pointer-events: auto;
}

.heading-component-title::after {
  display: inline-block;
  min-height: inherit;
  width: 0;
  vertical-align: middle;
}

.heading-component-title .icon {
  margin-right: 4px;
  vertical-align: middle;
}

.heading-component + * {
  margin-top: 25px;
}

.heading-component + .row {
  margin-top: 25px;
}

.heading-component-simple .heading-component-title {
  padding-left: 0;
}

.heading-component-simple .heading-component-title::before {
  display: none;
}

.bg-gray-100 .heading-component {
  border-bottom-color: #d7d7d7;
}

.time-decorated {
  display: inline-block;
  position: relative;
  padding: 0 90px;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.time-decorated::before,
.time-decorated::after {
  content: "";
  position: absolute;
  top: 0.66em;
  display: inline-block;
  vertical-align: middle;
  width: 75px;
  border-bottom: 1px solid #9b9b9b;
}

.time-decorated::before {
  left: 0;
}

.time-decorated::after {
  right: 0;
}

/*
*
* Brand
*/
.brand {
  display: inline-block;
}

.brand .brand-logo-dark {
  display: block;
}

.brand .brand-logo-light {
  display: none;
}

.brand-md img {
  max-width: 117px;
  max-height: 117px;
}

@media (min-width: 992px) and (max-width: 1599.98px) {
  .brand-md img {
    max-width: 90px;
    max-height: 90px;
  }
}

/*
*
* Links
*/
a {
  transition: all 0.15s ease-in-out;
}

a,
a:focus,
a:active,
a:hover {
  text-decoration: none;
}

a,
a:focus,
a:active {
  color: #64b000;
}

a:hover {
  color: #396400;
}

a[href*="tel"],
a[href*="mailto"] {
  white-space: nowrap;
}

.link {
  display: inline-block;
  line-height: 1.2;
}

.link-md {
  font-size: 16px;
}

.link-hover {
  color: #396400;
}

.link-press {
  color: #396400;
}

.link-circle,
.link-circle * {
  border-radius: 50%;
}

* + .privacy-link {
  margin-top: 40px;
}

.link-cart {
  color: #151515;
}

.link-cart > * + * {
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
}

.link-cart:hover {
  color: #64b000;
}

.link-cart:hover .link-cart-counter {
  color: #fff;
  background: #64b000;
}

.link-cart-hidden-link {
  display: none;
}

.link-cart-icon {
  font-size: 17px;
  line-height: 1;
  color: #151515;
}

.link-cart-counter {
  position: relative;
  top: -2px;
  height: 18px;
  min-width: 18px;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  border-radius: 50%;
  color: #fff;
  background: #64b000;
  transition: 0.22s;
}

.iphone .link-cart-counter {
  line-height: 17px;
}

button.link-cart {
  background: none;
  border: none;
  display: inline-block;
  padding: 0;
  outline: none;
  outline-offset: 0;
  cursor: pointer;
  -webkit-appearance: none;
}

button.link-cart::-moz-focus-inner {
  border: none;
  padding: 0;
}

