.list-terms dt + dd {
  margin-top: 15px;
}

.list-terms dd + dt {
  margin-top: 40px;
}

* + .list-terms {
  margin-top: 25px;
}

.index-list {
  counter-reset: li;
}

.index-list > li .list-index-counter:before {
  content: counter(li, decimal-leading-zero);
  counter-increment: li;
}

/**
* Marked list
*/
.list-marked {
  padding-left: 25px;
  text-align: left;
}

.list-marked > li {
  padding-left: 25px;
}

.list-marked > li::before {
  position: relative;
  display: inline-block;
  left: -25px;
  top: -1px;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  content: "";
  background: #d7d7d7;
}

* + .list-marked {
  margin-top: 15px;
}

p + .list-marked {
  margin-top: 10px;
}

.list-marked-categories {
  padding-left: 0;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-weight: 600;
  text-transform: uppercase;
}

.list-marked-categories > li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
}

.list-marked-categories > li a {
  flex: auto;
  padding-right: 15px;
  color: #151515;
}

.list-marked-categories > li a:hover {
  color: #64b000;
}

.list-marked-categories > li::before {
  left: -20px;
}

.list-marked-counter {
  color: #9b9b9b;
}

/**
* Ordered List
*/
.list-ordered {
  counter-reset: li;
  padding-left: 25px;
  text-align: left;
}

.list-ordered > li {
  position: relative;
  padding-left: 15px;
}

.list-ordered > li:before {
  content: counter(li, decimal) ".";
  counter-increment: li;
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 15px;
  color: #151515;
}

.list-ordered > li + li {
  margin-top: 10px;
}

* + .list-ordered {
  margin-top: 15px;
}

.list-tags {
  font-size: 0;
  margin-bottom: -6px;
  margin-left: -6px;
}

.list-tags:empty {
  margin-bottom: 0;
  margin-left: 0;
}

.list-tags > * {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 6px;
  margin-left: 6px;
}

.list-tags a {
  display: inline-block;
  padding: 9px 22px;
  overflow: hidden;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.05em;
  color: #151515;
  text-align: center;
  text-transform: uppercase;
  background: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 4px;
  transition: all 0.3s;
}

.list-tags a:hover {
  color: #fff;
  background: #64b000;
  border-color: #64b000;
}

.list-inline-bordered {
  transform: translate3d(0, -5px, 0);
  margin-bottom: -5px;
  margin-left: -12px;
  margin-right: -12px;
}

.list-inline-bordered > * {
  margin-top: 5px;
  padding-left: 12px;
  padding-right: 12px;
}

.list-inline-bordered > li {
  position: relative;
  vertical-align: middle;
}

.list-inline-bordered > li:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  height: 100%;
  max-height: 21px;
  right: -2px;
  width: 1px;
  background: rgba(235, 235, 235, 0.15);
  pointer-events: none;
}

.list-inline-bordered-lg {
  transform: translate3d(0, -15px, 0);
  margin-bottom: -15px;
  margin-left: -25px;
  margin-right: -25px;
}

.list-inline-bordered-lg > * {
  margin-top: 15px;
  padding-left: 25px;
  padding-right: 25px;
}

.list-inline-bordered-lg > li {
  text-align: left;
}

* + .list-inline-bordered {
  margin-top: 15px;
}

.footer-classic-dark .list-inline-bordered > li:not(:last-child)::after,
.context-dark .list-inline-bordered > li:not(:last-child)::after,
.rd-navbar-dark .list-inline-bordered > li:not(:last-child)::after,
.rd-navbar-darker .list-inline-bordered > li:not(:last-child)::after,
.bg-gray-700 .list-inline-bordered > li:not(:last-child)::after,
.bg-gray-2 .list-inline-bordered > li:not(:last-child)::after,
.bg-gray-4 .list-inline-bordered > li:not(:last-child)::after,
