.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid blue;
  border-right: 16px solid rgb(0, 243, 0);
  border-bottom: 16px solid red;
  border-left: 16px solid rgb(238, 8, 238);
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loaderBox {
  background-color: #000 !important;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 10;
  margin: auto;
  align-items: center;
  display: flex;
  justify-content: center;
}
.loaderBox2 {
  background-color: rgba(0, 0, 0, 0.171) !important;
  position: fixed;
  font-size: 30px;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 10000;
  margin: auto;
  align-items: center;
  display: flex;
  justify-content: center;
}
.loaderBox span {
  color: greenyellow;
  font-size: 200px;
}
@media (min-width: 600px) {
  .loaderBox span {
    font-size: 300px;
  }
}
