.isotope-filters > * {
  margin-top: 0;
  vertical-align: middle;
}

.isotope-filters .inline-list {
  position: relative;
  word-spacing: 0;
}

.isotope-filters .inline-list li {
  display: inline-block;
}

.isotope-filters .inline-list a {
  position: relative;
  transition: 0.3s;
  color: #2a2a2a;
}

.isotope-filters .inline-list .isotope-filters-trigger {
  display: none;
}

.isotope-filters-modern .inline-list {
  transform: translate3d(0, -10px, 0);
  margin-bottom: -10px;
  margin-left: -10px;
  margin-right: -10px;
}

.isotope-filters-modern .inline-list > * {
  margin-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.isotope-filters-modern .inline-list > li {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 18px;
}

.isotope-filters-modern .inline-list a {
  position: relative;
  padding-bottom: 10px;
}

.isotope-filters-modern .inline-list a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  transition: 0.4s;
  background: #64b000;
}

.isotope-filters-modern .inline-list a:hover {
  color: #64b000;
}

.isotope-filters-modern .inline-list a.active::after {
  width: 100%;
}

@media (min-width: 992px) {
  .isotope-filters-modern .inline-list {
    transform: translate3d(0, -10px, 0);
    margin-bottom: -10px;
    margin-left: -23px;
    margin-right: -23px;
  }
  .isotope-filters-modern .inline-list > * {
    margin-top: 10px;
    padding-left: 23px;
    padding-right: 23px;
  }
}

.isotope-modern-wrap [class*="col"] {
  margin-top: 30px;
}

@media (max-width: 767.98px) {
  .isotope-modern-wrap [class*="col"] {
    width: 100%;
    max-width: 100%;
  }
}

@media (min-width: 992px) {
  html:not(.tablet):not(.mobile) .isotope-modern-wrap [class*="col"] {
    margin-top: 0;
  }
}

.isotope-condensed {
  margin-left: 0;
  margin-right: 0;
}

.isotope-condensed .row > * {
  margin-top: -1px;
  margin-bottom: -1px;
}

* + .isotope {
  margin-top: 0;
}

* + .isotope-condensed {
  margin-top: 30px;
}

@media (max-width: 991.98px) {
  .isotope-condensed {
    padding: 0 0 30px;
    margin-left: 0;
    margin-right: 0;
  }
  .isotope-condensed .row.row-condensed > [class*="col"] {
    padding-left: 10px;
    padding-right: 10px;
  }
  .isotope-condensed .row.row-condensed > [class*="col"] + [class*="col"] {
    margin-top: 25px;
  }
}

@media (min-width: 768px) {
  * + .isotope {
    margin-top: 28px;
  }
  * + .isotope-condensed {
    margin-top: 50px;
  }
}

.isotope-filters {
  position: relative;
}

.isotope-filters-list {
  position: absolute;
  z-index: 1;
  /* padding: 15px; */
  background-color: #fff;
  box-shadow: -3px 0px 50px -2px rgba(0, 0, 0, 0.09);
  visibility: hidden;
  opacity: 0;
  display: flex;
  flex-wrap: wrap;
  transition: 0.3s ease-out opacity;
}

.isotope-filters-list.active {
  visibility: visible;
  opacity: 1;
}

.isotope-filters-list-item {
  width: 50%;
  position: relative;
}

.isotope-filters-list-link {
  display: block;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.075em;
  text-transform: uppercase;
  text-decoration: none;
}

.isotope-filters-list-link,
.isotope-filters-list-link:focus,
.isotope-filters-list-link:active {
  color: #151515;
  opacity: 0.38;
}

.isotope-filters-list-link.active,
.isotope-filters-list-link:hover {
  color: #151515;
  opacity: 1;
}

.isotope-filters-list-img {
  display: none;
  width: 100%;
  height: 50px;
  font-size: 50px;
  justify-content: center;
  align-items: flex-end;
}

.isotope-filters-list-img .sprite {
  display: block;
  font-size: 25px;
  color: green;
}

* + .isotope-filters-list-text {
  margin-top: 5px;
}

@media (min-width: 576px) {
  .isotope-filters-list {
    position: static;
    visibility: visible;
    opacity: 1;
    padding: 0;
    background-color: inherit;
    box-shadow: none;
  }
  .isotope-filters-list-item {
    width: 20%;
    padding: 25px 10px 25px;
  }
  .isotope-filters-list-item + .isotope-filters-list-item:before {
    content: "";
    position: absolute;
    left: 0;
    height: 52%;
    top: 50%;
    transform: translateY(-50%);
    border-left: 1px solid #d8d8d8;
  }
  .isotope-filters-list-item + .isotope-filters-list-item:nth-child(n + 6) {
    border-top: 1px solid #d8d8d8;
  }
  .isotope-filters-list-item + .isotope-filters-list-item:nth-child(6):before {
    display: none;
  }
  .isotope-filters-list-link {
    text-align: center;
  }
  .isotope-filters-list-img {
    display: flex;
  }
  .isotope-filters-list-text {
    display: block;
  }
  .isotope-filters-list-count {
    display: block;
    line-height: 1.2;
  }
}

@media (min-width: 1200px) {
  .isotope-filters-list {
    display: flex;
    justify-content: space-between;
  }
  .isotope-filters-list-item {
    width: 10%;
  }
  .isotope-filters-list-item + .isotope-filters-list-item:nth-child(n + 6) {
    border-top: none;
  }
  .isotope-filters-list-item + .isotope-filters-list-item:nth-child(6):before {
    display: block;
  }
  .isotope-filters-list-link.active:after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 6px 6px 6px;
    border-color: transparent transparent #2a2a2a transparent;
    transform: translateX(-50%);
  }
}

.isotope-filters-info {
  display: inline-block;
  width: 100%;
  padding: 15px 15px;
  color: #fff;
  background-color: #2a2a2a;
}

.isotope-filters-info-text {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-weight: 300;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

* + .isotope-filters-info,
.isotope-filters-list + .isotope-filters-info {
  margin-top: 5px;
}

@media (min-width: 768px) {
  .isotope-filters-list + .isotope-filters-info {
    margin-top: 0;
  }
}

.isotope-filters-toggle {
  display: block;
}

.isotope-filters-toggle,
.isotope-filters-toggle:focus,
.isotope-filters-toggle:active {
  color: #fff;
  border-color: #64b000;
  background-color: #64b000;
}

.isotope-filters-toggle.active,
.isotope-filters-toggle:hover {
  color: #2a2a2a;
  border-color: #ffd03a;
  background-color: #ffd03a;
}

@media (min-width: 576px) {
  .isotope-filters-toggle {
    display: none;
  }
}

