.sport-table-header {
  padding: 2px 23px 8px 23px;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #fff;
  background-color: #000000 !important;
  border-bottom: 3px solid greenyellow;
}
.sport-table-header2 {
  padding: 1px 1px 1px 5px;
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 10px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #000;
  background-color: silver;
}

.heading-component + .sport-table-header {
  margin-top: 30px;
}

.sport-table {
  background-color: #fff;
}

.sport-table-tr {
  padding: 11px 15px;
  border-left: 1px solid #e1e1e1;
  border-right: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
}

.sport-table-tr + .sport-table-tr {
  border-top: 1px solid #dddddd;
}

.sport-table-icon {
  text-align: center;
}

.sport-table-icon .sprite {
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  text-align: center;
  font-size: 30px;
}

.sport-table-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 10px;
  margin-bottom: 10px;
}

.sport-table-title-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 14px;
  margin-bottom: 10px;
}

.sport-table-title-item-right {
  align-items: center;
}

.sport-table-title-team,
.sport-table-title-score {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.2;
}

.sport-table-title-team {
  font-weight: 500;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #151515;
  font-size: 12px;
}

.sport-table-title-team + .sport-table-title-team {
  margin-top: 5px;
}

.sport-table-title-score {
  font-weight: 400;
  letter-spacing: 0.1em;
  color: #151515;
}

.sport-table-title-score span {
  margin-left: 12px;
}

.sport-table-title-score span:nth-last-child(1) {
  margin-left: 20px;
  color: #64b000;
}

.sport-table-wager {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: rgb(109, 100, 100) !important;
}

.sport-table-wager-button {
  flex-basis: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 7px;
  border-radius: 5px;
  font-size: 12px;

  letter-spacing: 0.025em;
  text-align: center;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
}

.sport-table-wager-button-count {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-weight: 600;
}

.sport-table-wager-button span {
  display: block;
  line-height: 1.2;
  text-decoration: none;
}

.sport-table-wager-button,
.sport-table-wager-button:focus,
.sport-table-wager-button:active {
  color: #151515;
  background-color: #eaecee;
  text-decoration: none;
}

.sport-table-wager-button:hover,
.sport-table-wager-button.active {
  color: #fff;
  background-color: #64b000;
}

* + .sport-table-wager-button {
  margin-top: 9px;
}

.sport-table-bonus {
  display: flex;
  align-items: center;
  justify-content: center;
}

.sport-table-bonus-count {
  font-family: "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.05em;
  color: #151515;
}

.sport-table-bonus-icon {
  display: flex;
  font-size: 20px;
  color: #9b9b9b;
}

@media (max-width: 576px) {
  .sport-table-icon .sprite {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
  }
  .sport-table-wager {
    flex-wrap: nowrap;
    flex-basis: 50%;
    max-width: 100%;
  }
  .sport-table-wager-double .sport-table-wager-button {
    flex-basis: 48%;
    max-width: 48%;
  }
  .sport-table-wager-button {
    flex-basis: 31.1%;
    max-width: 31.1%;
  }
  * + .sport-table-wager-button {
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .sport-table-title {
    border-right: 1px solid #e1e1e1;
  }
}

@media (min-width: 992px) {
  .sport-table-tr {
    padding-left: 0;
    padding-right: 0;
  }
  .sport-table-icon {
    padding-right: 13px;
    padding-left: 13px;
    padding-bottom: 0px;
  }
  .sport-table-icon .sprite {
    margin-left: auto;
    margin-right: auto;
  }
  .sport-table-wager {
    flex-wrap: nowrap;
  }
  .sport-table-wager-button {
    flex-basis: 31.1%;
    max-width: 31.1%;
  }
  * + .sport-table-wager-button {
    margin-top: 0;
  }
  .sport-table-bonus {
    justify-content: flex-start;
  }
}

@media (min-width: 1200px) {
  .sport-table-team + .sport-table-team {
    margin-top: 1px;
  }
}
